import React from "react";
import { Link } from "react-router-dom";

const Header = () => {
	return (
		<header className="header">
			<div>Great Jokes</div>
			<div className="menu">
				<div>
					<Link to="/jokes">All Jokes</Link>
				</div>
				<div>
					<Link to="/addjoke">Add A Joke</Link>
				</div>
			</div>
		</header>
	);
};

export default Header;
