import React from "react";
import { Link } from "react-router-dom";

const Joke = (props) => {
	return (
		<section className="joke">
			<div className="content">
				<div>
					<div>{props.joke.content}</div>
					<div>{props.joke.author}</div>
				</div>
				<div>
					<Link to={`/details/${props.joke.id}`}>
						<button>View FullScreen</button>
					</Link>
				</div>
			</div>
		</section>
	);
};

export default Joke;
