export const submitJoke = async (values) => {
	try {
		const response = await fetch(
			"https://ebrahim-2401d-default-rtdb.asia-southeast1.firebasedatabase.app/jokes.json",
			{
				method: "POST",
				body: JSON.stringify(values),
				header: { "content-type": "application/json" },
			}
		);

		if (!response.ok) {
			throw new Error("Error");
		}
	} catch (err) {
		console.log(err);
	}
};

export const fetchJokes = async () => {
	const response = await fetch(
		"https://ebrahim-2401d-default-rtdb.asia-southeast1.firebasedatabase.app/jokes.json"
	);

	const data = await response.json();
	const loadedJokes = [];

	for (const obj in data) {
		const joke = {
			id: obj,
			author: data[obj].author,
			content: data[obj].content,
		};
		loadedJokes.push(joke);
	}

	return loadedJokes;
};

export const fetchSingleJoke = async (id) => {
	try {
		const response = await fetch(
			`https://ebrahim-2401d-default-rtdb.asia-southeast1.firebasedatabase.app/jokes/${id}.json`
		);

		if (!response.ok) {
			throw new Error("Issue fetching the joke");
		}

		const data = await response.json();

		return data;
	} catch (err) {
		console.log(err.message);
	}
};
