import "./App.css";
import React, { Suspense } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import Header from "./components/header/Header";
import JokeList from "./components/jokes/JokeList";

const AddJoke = React.lazy(() => import("./components/jokes/AddJoke"));
const JokeDetail = React.lazy(() => import("./components/jokes/JokeDetail"));
function App() {
	return (
		<div className="centered">
			<Header />
			<Suspense fallback={<div>Loading...</div>}>
				<Switch>
					<Route path="/" exact>
						<Redirect to="/jokes" />
					</Route>
					<Route path="/jokes">
						<JokeList />
					</Route>
					<Route path="/addjoke">
						<AddJoke />
					</Route>
					<Route path="/details/:jokeId">
						<JokeDetail />
					</Route>
				</Switch>
			</Suspense>
		</div>
	);
}

export default App;
