import React, { useState, useEffect } from "react";
import Joke from "../jokes/Joke";
import { fetchJokes } from "../../fetch/fetch";

const sortJokes = (jokeArray, ascending) => {
	jokeArray.sort((jokeA, jokeB) => {
		if (ascending) {
			return jokeA.id > jokeB.id ? 1 : -1;
		} else {
			return jokeA.id < jokeB.id ? 1 : -1;
		}
	});
};
let loadedJokes = [];
const JokeList = () => {
	const [ascending, setAscending] = useState(false);
	const [loading, setLoading] = useState(false);

	const changeOrder = () => {
		setAscending((prev) => !prev);
	};

	useEffect(() => {
		setLoading(true);
		const loadJokes = async () => {
			return await fetchJokes();
		};

		loadJokes().then((response) => {
			loadedJokes = [...response];

			setLoading(false);
			return loadedJokes;
		});
	}, []);

	sortJokes(loadedJokes, ascending);

	return (
		<div>
			{loading && <p>Loading</p>}
			{!loading && (
				<React.Fragment>
					<button onClick={changeOrder}>
						{" "}
						Sort {ascending ? "descending" : "ascending"}
					</button>

					{loadedJokes.map((joke, index) => (
						<Joke key={index} joke={joke} />
					))}
				</React.Fragment>
			)}
			{loadedJokes.length === 0 && (
				<p>No Jokes found. Please add jokes to view</p>
			)}
		</div>
	);
};

export default JokeList;
